@use "variables";
@use "fonts";
@use "header";
@use "footer";
@use "index";
@use "pages";
@use "single";
@use "mobile";

* {
    font-family: "Montserrat", sans-serif;
}
h2 {
    font-size: variables.$font-size-large;
    font-weight: bold;
    span {
        font-weight: bold;
    }
}
.container-fluid {
    padding-left: 0;
    padding-right: 0;
}
// Botões
.btn {
    font-size: variables.$font-size-small;
    text-decoration: none;
    border: 1px solid;
    border-radius: 30px;
    padding: 6px 18px;
    transition: all ease 0.3s;
    &.btn-large {
        font-size: variables.$font-size-base;
        padding: 8px 32px;
    }
}
.primary-btn {
    background-color: variables.$primary-color;
    border-color: variables.$primary-color;
    color: variables.$secondary-color;
}
.primary-btn:hover {
    background-color: variables.$secondary-color;
    border-color: variables.$secondary-color;
    color: variables.$primary-color;
}
.secondary-btn {
    background-color: variables.$secondary-color;
    border-color: variables.$secondary-color;
    color: variables.$primary-color;
}
.secondary-btn:hover {
    background-color: variables.$primary-color;
    border-color: variables.$primary-color;
    color: variables.$secondary-color;
}
.transparent-btn {
    background-color: transparent;
    color: variables.$font-color;
}
.transparent-btn:hover {
    background-color: variables.$font-color;
    color: variables.$primary-color;
}

// Menu header
.menu {
    margin: 0;
    padding: 0;
    display: flex;
    gap: 15px;
    li {
        position: relative;
        list-style: none;
        &.menu-item-has-children {
            position: relative;
            padding-right: 12px;
            &::after {
                content: "▼";
                color: variables.$font-color;
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                font-size: 8px;
                pointer-events: none;
            }
            &:hover > .sub-menu {
                display: block;
            }
        }
        .sub-menu {
            display: none;
            position: absolute;
            top: 100%;
            left: 0;
            padding: 10px 0;
            background: rgba(255, 255, 255, 0.15);
            backdrop-filter: blur(20px);
            -webkit-backdrop-filter: blur(20px);
            min-width: 150px;
            width: max-content;
            li {
                padding: 5px 20px;
            }
        }
    }
    a {
        display: block;
        font-size: variables.$font-size-small;
        color: variables.$font-color;
        text-decoration: none;
        &:hover {
            color: variables.$font-color;
        }
    }
}
// Arrows slider
.slick-next,
.slick-prev {
    background-color: #fff;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    transition: all ease 0.3s;
    z-index: 1;
}
.slick-next:focus,
.slick-prev:focus {
    background-color: #fff;
    opacity: 1;
}
.slick-next:hover,
.slick-prev:hover {
    background-color: #fff;
    opacity: 0.7;
}
.slick-next {
    right: 5%;
}
.slick-prev {
    left: 5%;
}
.slick-next:before,
.slick-prev:before {
    font-family: none;
    font-size: 30px;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
}
.slick-next:before {
    content: "\25B8";
    color: #006635;
    left: 54%;
}
.slick-prev:before {
    content: "\25C2";
    color: #006635;
    left: 46%;
}
// Controller slider
.controls-slider {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 15px;
    button {
        display: flex;
        justify-content: center;
        align-items: center;
        background: transparent;
        border: 1px solid variables.$primary-color;
        border-radius: 50%;
        width: 30px;
        height: 30px;
        transition: all ease 0.3s;
        svg {
            path {
                fill: variables.$primary-color;
                transition: all ease 0.3s;
            }
        }
        &:hover {
            background: variables.$primary-color;
            svg {
                path {
                    fill: variables.$font-color;
                }
            }
        }
    }
}
// Scrollbar overflow slider
.scroll-slider {
    &::-webkit-scrollbar {
        width: 8px;
        height: 8px;
        background: transparent;
    }
    &::-webkit-scrollbar-thumb {
        background: rgba(0, 0, 0, 0);
        border-radius: 4px;
    }
}
// Modal case
.caseModal {
    width: 100dvw;
    height: 100dvh;
    background: #000000b3;
    .close-caseModal {
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        width: 20px;
        height: 20px;
        top: -10px;
        right: -10px;
        background-color: #fff;
        z-index: 1;
        border-radius: 50%;
        cursor: pointer;
        svg {
            fill: variables.$primary-color;
            width: 14px;
        }
    }
    .modal-content {
        background: transparent;
        width: 80dvw;
        height: 90dvh;
        left: 10dvw;
        top: 5dvh;
        border: none;
        .modal-body {
            padding: 0;
            .case-slider {
                visibility: hidden;
                height: 90dvh;
                .slick-next,
                .slick-prev {
                    width: 25px;
                    height: 25px;
                }
                .slick-prev {
                    left: -12.5px;
                }
                .slick-next {
                    right: -12.5px;
                }
                .slick-prev:before {
                    content: url("data:image/svg+xml;utf8,<svg xmlns=%22http://www.w3.org/2000/svg%22 viewBox=%220 0 640 640%22><path d=%22M164.7 331.3C158.5 325.1 158.5 314.9 164.7 308.7L372.7 100.7C378.9 94.5 389.1 94.5 395.3 100.7C401.5 106.9 401.5 117.1 395.3 123.3L198.6 320L395.3 516.7C401.5 522.9 401.5 533.1 395.3 539.3C389.1 545.5 378.9 545.5 372.7 539.3L164.7 331.3z%22/></svg>");
                    width: 16px;
                    height: 35px;
                    left: 50%;
                }
                .slick-next:before {
                    content: url("data:image/svg+xml;utf8,<svg xmlns=%22http://www.w3.org/2000/svg%22 viewBox=%220 0 640 640%22><path d=%22M475.3 308.7C481.5 314.9 481.5 325.1 475.3 331.3L267.3 539.3C261.1 545.5 250.9 545.5 244.7 539.3C238.5 533.1 238.5 522.9 244.7 516.7L441.4 320L244.7 123.3C238.5 117.1 238.5 106.9 244.7 100.7C250.9 94.5 261.1 94.5 267.3 100.7L475.3 308.7z%22/></svg>");
                    width: 16px;
                    height: 35px;
                    left: 50%;
                }

                .case-item {
                    height: 90dvh;
                    .thumbnail {
                        object-fit: cover;
                        object-position: top;
                        max-height: 80dvh;
                        border-radius: 5px;
                    }
                    .texts {
                        position: absolute;
                        left: 2%;
                        bottom: 0;
                        display: grid;
                        grid-template-columns: repeat(2, 1fr);
                        background-color: #fff;
                        border-radius: 5px;
                        padding: 15px;
                        width: 96%;
                        h3 {
                            font-size: variables.$font-size-medium !important;
                            font-weight: bold;
                        }
                        svg {
                            width: 20px;
                        }
                        span {
                            font-weight: 500;
                            gap: 8px;
                        }
                        .conteudo-case {
                            font-size: variables.$font-size-small;
                            line-height: variables.$line-height-font-size-small;
                        }
                    }
                }
                &.slick-initialized {
                    visibility: visible;
                }
            }
        }
    }
}
