@charset "UTF-8";
@media (min-width: 1200px) {
  .container,
  .container-lg,
  .container-md,
  .container-sm,
  .container-xl {
    max-width: 1240px;
  }
}
header {
  position: absolute;
  z-index: 1;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1882352941);
}
header .container {
  position: relative;
}
header .container .menu-header,
header .container .logo {
  display: flex;
  align-items: center;
}
header .container .menu-header {
  justify-content: flex-end;
}
header .container .menu-header #btn-header-orcamento {
  margin-right: 15px;
}
header .container .menu-header #icon-search {
  width: 20px;
  border-right: 1px solid rgba(255, 255, 255, 0.1882352941);
  padding: 30px 25px 30px 0;
  box-sizing: content-box;
}
header .container .menu-header #icon-search svg {
  cursor: pointer;
  transition: all ease 0.3s;
}
header .container .menu-header #icon-search svg:hover {
  transform: scale(1.2);
}
header .container .menu-header #secondary-menu-header {
  display: flex;
  align-items: center;
}
header .container .menu-header #secondary-menu-header nav {
  margin: 0 15px;
}
header .container .menu-header #icon-menu {
  position: relative;
  width: 30px;
  height: 10px;
  margin-left: 25px;
  cursor: pointer;
}
header .container .menu-header #icon-menu #icon-menu-top:before,
header .container .menu-header #icon-menu #icon-menu-bottom:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #fff;
  transition: all ease 0.3s;
}
header .container .menu-header #icon-menu #icon-menu-top:before {
  top: 0;
}
header .container .menu-header #icon-menu #icon-menu-bottom:before {
  bottom: 0;
}
header .container .menu-header #icon-menu:hover #icon-menu-top:before {
  top: -2px;
}
header .container .menu-header #icon-menu:hover #icon-menu-bottom:before {
  bottom: -2px;
}
header .container #div-search {
  position: absolute;
  padding: 10px 0;
  width: 100%;
}
header .container #div-search input {
  color: #006635;
  background: rgba(255, 255, 255, 0.6);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: none;
  border-radius: 30px;
  padding: 0 15px;
  outline: none;
  height: 0;
  width: 100%;
  transition: all ease 0.3s;
}
header .container #div-search.active input {
  padding: 5px 15px;
  height: 40px;
}

#slider-topo {
  margin-bottom: 0;
  width: 100%;
}
#slider-topo .slick-prev {
  display: none !important;
}
#slider-topo .slider-item img,
#slider-topo .slider-item video {
  width: 100%;
  height: 100vh;
  object-fit: cover;
  object-position: top;
}
#slider-topo .container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
#slider-topo .container .slider-text {
  width: 30%;
}
#slider-topo .container .slider-text h2 {
  font-size: 30px;
  line-height: 37px;
  font-weight: bold;
  color: #cae098;
}
#slider-topo .container .slider-text h2::first-line,
#slider-topo .container .slider-text p {
  line-height: 20px;
  color: #fff;
}
#slider-topo .container .slider-text p {
  margin-bottom: 0;
}
#slider-topo .container a {
  margin-top: 20px;
}
#slider-topo .slick-dots {
  bottom: 5%;
  right: 5%;
  width: max-content;
}
#slider-topo .slick-dots li {
  margin: 0;
}
#slider-topo .slick-dots li button:before {
  content: "";
  background: #fff;
  color: #fff;
  transition: all ease 0.3s;
  border: 2px solid #fff;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  opacity: 1;
}
#slider-topo .slick-dots li.slick-active button:before {
  border-color: #a4c659;
}

#nossos-servicos {
  background: #d9d9d9;
  padding: 80px 0;
}
#nossos-servicos .title {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  border-left: 5px solid #006635;
  padding-left: 50px;
  height: 90px;
}
#nossos-servicos .title span {
  color: #006635;
}
#nossos-servicos .title p {
  font-weight: 500;
  margin-bottom: 0;
}
#nossos-servicos #slider-nossos-servicos {
  padding-top: 60px;
  overflow: hidden;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos {
  flex-wrap: nowrap;
  gap: 15px;
  padding-left: 15px;
  padding-right: 100px;
  overflow: auto;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item {
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  padding: 0;
  border-radius: 5px;
  width: 450px;
  cursor: pointer;
  transition: all ease 0.3s;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item .left-title {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  writing-mode: sideways-lr;
  text-orientation: mixed;
  line-height: 1.1;
  white-space: nowrap;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item .left-title h3 {
  color: #dbdbdb;
  opacity: 0.35;
  font-weight: bold;
  padding: 15px 0;
  margin: 0;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item .content-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
  padding-bottom: 25px;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item .content-item img {
  max-width: 100%;
  border-radius: 0 5px 0 0;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item .content-item h3 {
  font-size: 20px;
  font-weight: bold;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item .content-item h4 {
  font-size: 16px;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item .content-item .saiba-mais {
  width: max-content;
  min-width: 140px;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item .content-item .saiba-mais:hover {
  background-color: #000;
  color: #fff;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item.active .left-title,
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item.active .saiba-mais, #nossos-servicos #slider-nossos-servicos .row-items-servicos .item.desactive .left-title,
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item.desactive .saiba-mais {
  display: none;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item.active .content-item, #nossos-servicos #slider-nossos-servicos .row-items-servicos .item.desactive .content-item {
  position: relative;
  width: 100%;
  padding-bottom: 0;
  height: 400px;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item.active .content-item img, #nossos-servicos #slider-nossos-servicos .row-items-servicos .item.desactive .content-item img {
  border-radius: 5px;
  height: 100%;
  object-fit: cover;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item.active .content-item .titles, #nossos-servicos #slider-nossos-servicos .row-items-servicos .item.desactive .content-item .titles {
  position: absolute;
  left: 5%;
  bottom: 10%;
  color: #fff;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item.active {
  width: 60%;
}
#nossos-servicos #slider-nossos-servicos .row-items-servicos .item.desactive {
  width: 25%;
}
#nossos-servicos .content-servico h2,
#nossos-servicos .content-servico h3 {
  font-size: 16px;
}
#nossos-servicos .content-servico .subservicos-wrapper {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: calc(12.5% + 0.5px);
  gap: 60px;
  overflow-x: auto;
  overflow-y: hidden;
  scroll-snap-type: x mandatory;
}
#nossos-servicos .content-servico .subservicos-wrapper .slick-prev {
  left: 0;
}
#nossos-servicos .content-servico .subservicos-wrapper .slick-next {
  right: 0;
}
#nossos-servicos .content-servico .subservicos-wrapper .subservico-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 15px;
}
#nossos-servicos .content-servico .subservicos-wrapper .subservico-item h3 {
  margin-bottom: 0;
}
#nossos-servicos .content-servico .cases-wrapper {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: calc(32% - 1px);
  gap: 25px;
  overflow-x: auto;
  overflow-y: hidden;
  scroll-snap-type: x mandatory;
}
#nossos-servicos .content-servico .cases-wrapper .slick-prev {
  left: 0;
}
#nossos-servicos .content-servico .cases-wrapper .slick-next {
  right: 0;
}
#nossos-servicos .content-servico .cases-wrapper .case-item {
  position: relative;
  cursor: pointer;
}
#nossos-servicos .content-servico .cases-wrapper .case-item .thumbnail {
  position: relative;
  border-radius: 5px;
}
#nossos-servicos .content-servico .cases-wrapper .case-item .chapeu-case {
  position: absolute;
  top: 5%;
  left: 5%;
  background-color: #cae098;
  color: #006635;
  font-size: 12px;
  padding: 3px 12px;
  border-radius: 30px;
}
#nossos-servicos .content-servico .cases-wrapper .case-item .texts {
  position: absolute;
  bottom: 5%;
  left: 5%;
  color: #fff;
  width: 90%;
  z-index: 1;
}
#nossos-servicos .content-servico .cases-wrapper .case-item .texts h3 {
  font-size: 20px;
  font-weight: bold;
}
#nossos-servicos .content-servico .cases-wrapper .case-item .texts svg {
  width: 20px;
}
#nossos-servicos .content-servico .cases-wrapper .case-item .texts span {
  font-size: 16px;
}
#nossos-servicos .content-servico .cases-wrapper .case-item .texts img {
  max-width: 80px;
  height: auto;
  border-radius: 0;
}
#nossos-servicos .content-servico .cases-wrapper .case-item::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, #000 100%);
  border-radius: 5px;
}

* {
  font-family: "Montserrat", sans-serif;
}

h2 {
  font-size: 30px;
  font-weight: bold;
}
h2 span {
  font-weight: bold;
}

.container-fluid {
  padding-left: 0;
  padding-right: 0;
}

.btn {
  font-size: 14px;
  text-decoration: none;
  border: 1px solid;
  border-radius: 30px;
  padding: 6px 18px;
  transition: all ease 0.3s;
}
.btn.btn-large {
  font-size: 16px;
  padding: 8px 32px;
}

.primary-btn {
  background-color: #006635;
  border-color: #006635;
  color: #cae098;
}

.primary-btn:hover {
  background-color: #cae098;
  border-color: #cae098;
  color: #006635;
}

.secondary-btn {
  background-color: #cae098;
  border-color: #cae098;
  color: #006635;
}

.secondary-btn:hover {
  background-color: #006635;
  border-color: #006635;
  color: #cae098;
}

.transparent-btn {
  background-color: transparent;
  color: #fff;
}

.transparent-btn:hover {
  background-color: #fff;
  color: #006635;
}

.menu {
  margin: 0;
  padding: 0;
  display: flex;
  gap: 15px;
}
.menu li {
  position: relative;
  list-style: none;
}
.menu li.menu-item-has-children {
  position: relative;
  padding-right: 12px;
}
.menu li.menu-item-has-children::after {
  content: "▼";
  color: #fff;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  font-size: 8px;
  pointer-events: none;
}
.menu li.menu-item-has-children:hover > .sub-menu {
  display: block;
}
.menu li .sub-menu {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  padding: 10px 0;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  min-width: 150px;
  width: max-content;
}
.menu li .sub-menu li {
  padding: 5px 20px;
}
.menu a {
  display: block;
  font-size: 14px;
  color: #fff;
  text-decoration: none;
}
.menu a:hover {
  color: #fff;
}

.slick-next,
.slick-prev {
  background-color: #fff;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  transition: all ease 0.3s;
  z-index: 1;
}

.slick-next:focus,
.slick-prev:focus {
  background-color: #fff;
  opacity: 1;
}

.slick-next:hover,
.slick-prev:hover {
  background-color: #fff;
  opacity: 0.7;
}

.slick-next {
  right: 5%;
}

.slick-prev {
  left: 5%;
}

.slick-next:before,
.slick-prev:before {
  font-family: none;
  font-size: 30px;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
}

.slick-next:before {
  content: "▸";
  color: #006635;
  left: 54%;
}

.slick-prev:before {
  content: "◂";
  color: #006635;
  left: 46%;
}

.controls-slider {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 15px;
}
.controls-slider button {
  display: flex;
  justify-content: center;
  align-items: center;
  background: transparent;
  border: 1px solid #006635;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  transition: all ease 0.3s;
}
.controls-slider button svg path {
  fill: #006635;
  transition: all ease 0.3s;
}
.controls-slider button:hover {
  background: #006635;
}
.controls-slider button:hover svg path {
  fill: #fff;
}

.scroll-slider::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  background: transparent;
}
.scroll-slider::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0);
  border-radius: 4px;
}

.caseModal {
  width: 100dvw;
  height: 100dvh;
  background: rgba(0, 0, 0, 0.7019607843);
}
.caseModal .close-caseModal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 20px;
  height: 20px;
  top: -10px;
  right: -10px;
  background-color: #fff;
  z-index: 1;
  border-radius: 50%;
  cursor: pointer;
}
.caseModal .close-caseModal svg {
  fill: #006635;
  width: 14px;
}
.caseModal .modal-content {
  background: transparent;
  width: 80dvw;
  height: 90dvh;
  left: 10dvw;
  top: 5dvh;
  border: none;
}
.caseModal .modal-content .modal-body {
  padding: 0;
}
.caseModal .modal-content .modal-body .case-slider {
  visibility: hidden;
  height: 90dvh;
}
.caseModal .modal-content .modal-body .case-slider .slick-next,
.caseModal .modal-content .modal-body .case-slider .slick-prev {
  width: 25px;
  height: 25px;
}
.caseModal .modal-content .modal-body .case-slider .slick-prev {
  left: -12.5px;
}
.caseModal .modal-content .modal-body .case-slider .slick-next {
  right: -12.5px;
}
.caseModal .modal-content .modal-body .case-slider .slick-prev:before {
  content: url("data:image/svg+xml;utf8,<svg xmlns=%22http://www.w3.org/2000/svg%22 viewBox=%220 0 640 640%22><path d=%22M164.7 331.3C158.5 325.1 158.5 314.9 164.7 308.7L372.7 100.7C378.9 94.5 389.1 94.5 395.3 100.7C401.5 106.9 401.5 117.1 395.3 123.3L198.6 320L395.3 516.7C401.5 522.9 401.5 533.1 395.3 539.3C389.1 545.5 378.9 545.5 372.7 539.3L164.7 331.3z%22/></svg>");
  width: 16px;
  height: 35px;
  left: 50%;
}
.caseModal .modal-content .modal-body .case-slider .slick-next:before {
  content: url("data:image/svg+xml;utf8,<svg xmlns=%22http://www.w3.org/2000/svg%22 viewBox=%220 0 640 640%22><path d=%22M475.3 308.7C481.5 314.9 481.5 325.1 475.3 331.3L267.3 539.3C261.1 545.5 250.9 545.5 244.7 539.3C238.5 533.1 238.5 522.9 244.7 516.7L441.4 320L244.7 123.3C238.5 117.1 238.5 106.9 244.7 100.7C250.9 94.5 261.1 94.5 267.3 100.7L475.3 308.7z%22/></svg>");
  width: 16px;
  height: 35px;
  left: 50%;
}
.caseModal .modal-content .modal-body .case-slider .case-item {
  height: 90dvh;
}
.caseModal .modal-content .modal-body .case-slider .case-item .thumbnail {
  object-fit: cover;
  object-position: top;
  max-height: 80dvh;
  border-radius: 5px;
}
.caseModal .modal-content .modal-body .case-slider .case-item .texts {
  position: absolute;
  left: 2%;
  bottom: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  background-color: #fff;
  border-radius: 5px;
  padding: 15px;
  width: 96%;
}
.caseModal .modal-content .modal-body .case-slider .case-item .texts h3 {
  font-size: 20px !important;
  font-weight: bold;
}
.caseModal .modal-content .modal-body .case-slider .case-item .texts svg {
  width: 20px;
}
.caseModal .modal-content .modal-body .case-slider .case-item .texts span {
  font-weight: 500;
  gap: 8px;
}
.caseModal .modal-content .modal-body .case-slider .case-item .texts .conteudo-case {
  font-size: 14px;
  line-height: 18px;
}
.caseModal .modal-content .modal-body .case-slider.slick-initialized {
  visibility: visible;
}